import React from "react";
import { Helmet } from 'react-helmet';
import './Home.css';

import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/swiper.min.css";
import "swiper/components/pagination/pagination.min.css"
import "swiper/components/navigation/navigation.min.css"




import coverimage from '../Images/Home/Coverimage.jpg';
import brandimage from '../Images/Home/Brandimage.jpg';

import busi1image from '../Images/Home/Business1.jpg';
import busi2image from '../Images/Home/Business2.jpg';
import busi3image from '../Images/Home/Business3.jpg';
import busi4image from '../Images/Home/Business4.jpg';

// Client Logos
import apimage from '../Images/Home/Image1.jpg';
import armyimage from '../Images/Home/Image2.jpg';
import assamimage from '../Images/Home/Image3.jpg';
import csirimage from '../Images/Home/Image4.jpg';
import dellimage from '../Images/Home/Image5.jpg';
import dgcaimage from '../Images/Home/Image6.jpg';
import drdoimage from '../Images/Home/Image7.jpg';



import SwiperCore, {
    Autoplay, Pagination, Navigation, EffectCoverflow
} from 'swiper/core';

SwiperCore.use([Autoplay, Pagination, Navigation, EffectCoverflow]);


export default function Home() {
    return (
        <>
            <Helmet>
                <title>Printlike | Printing Solutions for Offices, Businesses, Events </title>
                <meta name="description" content="Printing Solutions" />
            </Helmet>
            <div className="container-fluid p-0">
                <div className="row pb-4">
                    <div className="col-sm-12">
                        <div className="img-div">
                            <img
                                src={coverimage}
                                className="cover-image"
                                alt="" />
                        </div>
                    </div>
                </div>
            </div>

            <div className="container-fluid">
                <div className="container py-5">
                    <div className="row">
                        <div className="col-sm-12">
                            <h2><b>Most Popular</b></h2>
                            <Swiper effect={'coverflow'} grabCursor={true} slidesPerView={3} spaceBetween={0} centeredSlides={false} autoplay={{
                                "delay": 2000,
                                "disableOnInteraction": false
                            }} coverflowEffect={{
                                "rotate": 50,
                                "stretch": 0,
                                "depth": 100,
                                "modifier": 1,
                                "slideShadows": false
                            }} navigation={false} className="mySwiper">
                                <SwiperSlide><a href="/About"><img
                                    src={apimage}
                                    className=""
                                    alt="Survey Application of Drones" /></a><br /></SwiperSlide>
                                <SwiperSlide><img
                                    src={armyimage}
                                    className="-image"
                                    alt="Survey Application of Drones" /></SwiperSlide>
                                <SwiperSlide><img
                                    src={assamimage}
                                    className="-image"
                                    alt="Survey Application of Drones" /></SwiperSlide>
                                <SwiperSlide><img
                                    src={csirimage}
                                    className="-image"
                                    alt="Survey Application of Drones" /></SwiperSlide>
                                <SwiperSlide><img
                                    src={dellimage}
                                    className="-image"
                                    alt="Survey Application of Drones" /></SwiperSlide>
                                <SwiperSlide><img
                                    src={dgcaimage}
                                    className="-image"
                                    alt="Survey Application of Drones" /></SwiperSlide>
                                <SwiperSlide><img
                                    src={drdoimage}
                                    className="-image"
                                    alt="Survey Application of Drones" /></SwiperSlide>
                            </Swiper>
                        </div>
                    </div>
                </div >
            </div>

            <div className="container-fluid">
                <div className="container py-5">
                    <div className="row">
                        <div className="col-sm-12">
                            <img
                                src={brandimage}
                                className="brand-image"
                                alt="Survey Application of Drones" />
                        </div>
                    </div>
                </div>
            </div>


            <div className="container-fluid">
                <div className="container py-5">
                    <div className="row pb-3">
                        <h2>Business Essentials</h2>
                    </div>
                    <div className="row">
                        <div className="col-sm-3">
                            <img
                                src={busi1image}
                                className="busi-image"
                                alt="Survey Application of Drones" />
                            <h3 className="text-center pt-2">ID Cards</h3>
                        </div>
                        <div className="col-sm-3">
                            <img
                                src={busi2image}
                                className="busi-image"
                                alt="Survey Application of Drones" />
                            <h3 className="text-center pt-2">Lanyards</h3>
                        </div>
                        <div className="col-sm-3">
                            <img
                                src={busi3image}
                                className="busi-image"
                                alt="Survey Application of Drones" />
                            <h3 className="text-center pt-2">Business Cards</h3>
                        </div>
                        <div className="col-sm-3">
                            <img
                                src={busi4image}
                                className="busi-image"
                                alt="Survey Application of Drones" />
                            <h3 className="text-center pt-2">Letterheads</h3>
                        </div>
                    </div>
                </div>
            </div>

            <div className="container-fluid">
                <div className="container py-5">
                    <div className="row">
                        <div className="col-sm-6">
                            <h1>Print Like: Here for <br />
                                small business<br />
                                since 1995.</h1>
                            <p>
                                For more than 20 years, Vistaprint has helped small business owners, entrepreneurs and dreamers create custom designs and professional marketing. Our online printing services are intended to help you find custom products you need – business cards, promotional marketing and more – to create a look you love.
                            </p>
                        </div>
                        <div className="col-sm-6">
                            <h2>Easy Design</h2>
                            <p>Our online tools make the process as simple and clear as possible, and we’re working to improve your experience all the time.</p>
                            <h2>Make It Match</h2>
                            <p>Our online tools make the process as simple and clear as possible, and we’re working to improve your experience all the time.</p>
                            <h2>Absolutely Guaranteed</h2>
                            <p>Our online tools make the process as simple and clear as possible, and we’re working to improve your experience all the time.</p>
                        </div>
                    </div>
                </div>
            </div>


        </>
    )
}

